import Vue from 'vue'
import VueRouter from 'vue-router'
import About from '../components/About.vue'
import Skill from '../components/Skill.vue'
import project from '../components/project.vue'

Vue.use(VueRouter)
export default new VueRouter({
  routes: [
    {
      path: '/about',
      name: 'Page 1',
      component: About,
    },
    {
      path: '/skill',
      name: 'Page 2',
      component: Skill,
    },
    {
      path: '/project',
      name: 'Page Three',
      component: project,
    },
    { path: '*', redirect: '/about' }
  ]
})